import * as yup from 'yup';

const contentSchema = yup.object().shape({
    name: yup.string().required('erik ad bo braklamaz.'),
    extraFee: yup.number().min(0, 'Ek cret sfr veya pozitif bir deer olmaldr.').required('Ek cret bo braklamaz.'),
});

export const productSchema = yup.object().shape({
    productName: yup
        .string()
        .required('rn ad bo braklamaz.'),

    productCategory: yup
        .string()
        .required('Kategori bo braklamaz.'),

    sizes: yup.array().of(
        yup.object().shape({
            size: yup.string().required('Boyut ad bo braklamaz.'),
            extraFee: yup.number().min(0, 'Ek cret sfr veya pozitif bir deer olmaldr.')
        })
    ).required('En az bir boyut eklenmelidir.'),

    contents: yup.array().of(contentSchema).required('En az bir rn ierii eklenmelidir.'),
});
