// ../helper/stringTurkish.js

const turkishToUpper = (str) => {
    const letters = { "i": "", "": "", "": "", "": "", "": "", "": "", "": "I" };
    return str.replace(/(([i]))/g, (letter) => letters[letter]).toUpperCase();
};

const turkishToLower = (str) => {
    const letters = { "": "i", "I": "", "": "", "": "", "": "", "": "", "": "" };
    return str.replace(/(([I]))/g, (letter) => letters[letter]).toLowerCase();
};

const toUpperOnlyFirstChar = (str) => {
    str = str.replaceAll(/\s+/g, ' ');
    let strings = str.split(" ");
    for (let i = 0; i < strings.length; i++) {
        strings[i] = strings[i][0].toUpperCase() + strings[i].substring(1).toLowerCase();
    }
    return strings.join(" ");
};

module.exports = {
    turkishToUpper,
    turkishToLower,
    toUpperOnlyFirstChar
};
