import * as yup from 'yup'

const passwordRules = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{5,}$/;
const phoneRules = /^[0-9]{10}$/;
const usernameRules = /^\S*$/;

export const userSchema = (isEditMode) => yup.object().shape({

    firstname: yup
        .string()
        .min(3, 'sminiz en az ${min} karakter iermelidir.')
        .max(15, 'sminiz ${max} karakterden daha fazla olamaz.')
        .required('sim bo braklamaz.'),

    lastname: yup
        .string()
        .min(3, 'Soyisminiz en az ${min} karakter iermelidir.')
        .max(15, 'Soyisminiz ${max} karakterden daha fazla olamaz.')
        .required('Soyisim bo braklamaz.'),

    username: yup
        .string()
        .min(3, 'Kullanc adnz en az ${min} karakter iermelidir.')
        .max(15, "Kullanc adnz ${max} karakterden daha fazla olamaz.")
        .matches(usernameRules, {
            message: 'Kullanc ad boluk iermemelidir.',
        })
        .required('Kullanc ad braklamaz.'),

    phone: yup
        .string()
        .matches(phoneRules, {
            message: 'Geerli bir telefon numaras giriniz.',
        }),

    // ifre alanlarn sadece yeni kullanc oluturma ilemi iin zorunlu yapyoruz
    ...(isEditMode ? {} : {
        password: yup
            .string()
            .required('Ltfen bir ifre belirleyiniz.')
            .min(6, 'ifre en az ${min} haneli olmaldr.')
            .matches(passwordRules, {
                message: 'ifre en az bir byk, bir kk harf ve bir say iermelidir.',
            }),

        confirmPassword: yup
            .string()
            .oneOf([yup.ref('password')], 'ifreler elemiyor')
            .required('Tekrar ifre girmek zorunludur'),
    }),

    permissions: yup
        .string()
        .required('Ltfen en az bir yetki seiniz')

});