import '../index.css';
import React, {useContext, useEffect, useState} from 'react';
import {
    Badge,
    Stack,
    Chip,
    Grid,
    Typography,
    Box,
    TextField,
    List,
    ListItem,
    ListItemText,
    Dialog,
    DialogContent,
    DialogActions,
    Button,
    Select,
    MenuItem,
    InputLabel,
    FormControl,
    FormHelperText,
    IconButton,
    Card,
    CardContent,
    CardActions,
    useMediaQuery,
    useTheme
} from '@mui/material';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import CloseIcon from '@mui/icons-material/Close';
import {SocketContext} from '../context/SocketContext';
import {turkishToLower} from '../helper/stringTurkish';
import SearchIcon from '@mui/icons-material/Search';
import DeleteIcon from '@mui/icons-material/Delete';
import ClearIcon from '@mui/icons-material/Clear';
import EditIcon from '@mui/icons-material/Edit';
import OrderSummary from "../components/OrderSummary";

export default function OrderEntry() {
    const {sendSocketMessage, socketData, isConnected} = useContext(SocketContext);
    const [products, setProducts] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [selectedProduct, setSelectedProduct] = useState(null);
    const [open, setOpen] = useState(false);
    const [quantity, setQuantity] = useState(1);
    const [selectedSize, setSelectedSize] = useState('');
    const [selectedContent, setSelectedContent] = useState('');
    const [orders, setOrders] = useState([]);
    const [showCart, setShowCart] = useState(false);
    const [editOrderIndex, setEditOrderIndex] = useState(null);
    const [sizeError, setSizeError] = useState(false);
    const messageType = 'getProducts';
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

    const calculateOrderPrice = (order) => {

        const product = order.product;
        const selectedSize = order.size;
        const selectedContent = order.content;
        const quantity = order.quantity;


        if (!product || !product.sizes || quantity <= 0) return 0;

        // Boyut fiyatn bul
        const size = product.sizes.find(size => size.size === selectedSize);
        if (!size) return 0;

        // erik ek creti
        const content = product.contents.find(content => content.name === selectedContent);
        const contentFee = content ? content.extraFee : 0;

        // Toplam fiyat hesapla
        return (size.price + contentFee) * quantity;
    }

    const calculateTotalPrice = () => {
        return orders.reduce((total, order) => total + calculateOrderPrice(order), 0);
    };

    const handleDeleteOrder = (orderIndex) => {
        setOrders(prevOrders => prevOrders.filter((_, index) => index !== orderIndex));
    };

    const handleEditOrder = (index) => {
        const order = orders[index];
        setSelectedProduct(order.product);
        setQuantity(order.quantity);
        setSelectedSize(order.size);
        setSelectedContent(order.content || '');
        setEditOrderIndex(index);
        setOpen(true);
    };


    useEffect(() => {
        if (isConnected) {
            sendSocketMessage({}, messageType);
        }
        // eslint-disable-next-line
    }, [isConnected]);

    useEffect(() => {
        if (socketData && socketData.type === messageType) {
            if (socketData.message && socketData.message.status === 'success' && socketData.message.products) {
                setProducts(socketData.message.products);
            }
        }
    }, [socketData]);

    const handleSearchChange = (event) => {
        setSearchTerm(event.target.value);
    };

    const handleProductClick = (product) => {
        setSelectedProduct(product);
        setQuantity(1);
        setSelectedSize(product.sizes.length === 1 ? product.sizes[0].size : '');
        setSelectedContent(product.contents.length > 0 ? product.contents[0].name : '');
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
        setEditOrderIndex(null);
        setSizeError(false);
    };

    const handleAddToOrder = () => {

        // Boyut seilmemise hata durumunu gncelle
        if (!selectedSize) {
            setSizeError(true);
            return;
        }
        setSizeError(false);


        // erik seilmesi gereken bir rn varsa, ierik seilmemise kullancy bilgilendir
        if (selectedProduct.contents.length > 0 && !selectedContent) {
            alert('Ltfen ierik sein!');
            return;
        }

        // Yeni sipari nesnesi olutur
        const newOrder = {
            product: selectedProduct,
            quantity,
            size: selectedSize,
            content: selectedContent || ''
        };

        setOrders(prevOrders => {
            let updatedOrders = [...prevOrders];
            const existingOrderIndex = updatedOrders.findIndex(order =>
                order.product.id === newOrder.product.id &&
                order.size === newOrder.size &&
                order.content === newOrder.content
            );

            if (existingOrderIndex !== -1) {

                if (existingOrderIndex === editOrderIndex)
                    updatedOrders[existingOrderIndex].quantity = newOrder.quantity;
                else {
                    updatedOrders[existingOrderIndex].quantity += newOrder.quantity;
                    updatedOrders = updatedOrders.filter((_, index) => index !== editOrderIndex);
                }

            } else {
                updatedOrders.push(newOrder);
            }

            return updatedOrders;
        });

        setOpen(false);
    };


    const filteredProducts = products.filter(product => {
        const searchTermLower = turkishToLower(searchTerm);
        const productNameMatch = turkishToLower(product.productName).includes(searchTermLower);
        const productCategoryMatch = turkishToLower(product.productCategory).includes(searchTermLower);
        const contentMatch = product.contents.some(content =>
            turkishToLower(content.name).includes(searchTermLower)
        );
        return productNameMatch || productCategoryMatch || contentMatch;
    });

    const handleCartToggle = () => {
        setShowCart(!showCart);
    };

    return (
        <>
            <Grid container spacing={0} sx={{height: 'calc(100vh - 64px)', overflow: 'hidden'}}>
                {/* Sol Taraf - rnler */}
                <Grid item xs={12} sm={orders.length > 0 ? 6 : 12}
                      sx={{borderRight: '1px solid #ddd', display: isMobile && showCart ? 'none' : 'block'}}>
                    <Box
                        sx={{
                            display: 'flex',
                            flexDirection: 'column',
                            height: 'calc(100vh - 64px)', // Balk veya st blm varsa bu ykseklii ayarlayn
                            overflow: 'hidden'
                        }}
                    >
                        {/* rnler Bal */}
                        <Box
                            sx={{
                                display: 'flex', alignItems: 'center', justifyContent: 'space-around',
                                padding: 2,
                                gap: 2,
                                backgroundColor: '#fff',
                                borderBottom: '1px solid #ddd',
                                position: 'sticky',
                                top: 0,
                                zIndex: 1
                            }}
                        >
                            <Typography variant="h6">rnler</Typography>
                            <Box sx={{width: 0.6, display: 'flex', justifyContent: 'center'}}>
                                <TextField
                                    variant="outlined"
                                    placeholder="rn ara..."
                                    value={searchTerm}
                                    onChange={handleSearchChange}
                                    size="small"
                                    InputProps={{
                                        startAdornment: (
                                            <IconButton>
                                                <SearchIcon />
                                            </IconButton>
                                        ),
                                        endAdornment: (
                                            <IconButton
                                                onClick={() => setSearchTerm('')}
                                                edge="end"
                                                sx={{ visibility: searchTerm ? 'visible' : 'hidden' }}
                                            >
                                                <ClearIcon />
                                            </IconButton>
                                        )
                                    }}
                                    sx={{width: '100%'}}
                                />
                            </Box>
                        </Box>

                        {/* rnler Listesi */}
                        <Box className="custom-scrollbar"
                             sx={{
                                 padding: 2,
                                 overflowY: 'auto',
                                 flex: 1 // Kalan alann tamamn kaplar
                             }}
                        >
                            <List>
                                {filteredProducts.map(product => (
                                    <ListItem
                                        key={product.id}
                                        onClick={() => handleProductClick(product)}
                                        sx={{
                                            '&:hover': {backgroundColor: '#f5f5f5'},
                                            display: 'flex',
                                            justifyContent: 'space-between'
                                        }}
                                    >
                                        <ListItemText
                                            primary={product.productName}
                                            secondary={product.productCategory}
                                        />
                                        <Box sx={{display: 'flex', flexWrap: 'wrap', gap: 1}}>
                                            {product.contents.map(content => (
                                                <Chip
                                                    size='small'
                                                    key={content.name}
                                                    label={`${content.name}`}
                                                    sx={{
                                                        fontSize: {
                                                            xs: '0.6rem', // Small screens
                                                            sm: '0.7rem', // Medium screens
                                                            md: '1rem' // Large screens
                                                        },
                                                        fontStyle: 'italic'
                                                    }}
                                                />
                                            ))}
                                        </Box>
                                    </ListItem>
                                ))}
                            </List>
                        </Box>
                    </Box>
                </Grid>


                {/* Sa Taraf - Sipari Ayrntlar */}
                {orders.length > 0 && (
                    <Grid
                        item
                        xs={12}
                        sm={6}
                        md={6}
                        sx={{
                            height: 'calc(100vh)',
                            display: isMobile && !showCart ? 'none' : 'block'
                        }} // 64px: Sipari zeti ykseklii
                    >
                        <Box
                            sx={{
                                backgroundColor: '#f5f4f6',
                                padding: 1,
                                borderRadius: 1,
                                height: 1,
                                display: 'flex',
                                flexDirection: 'column'
                            }}
                        >
                            {/* Sabit Balk */}
                            <Box
                                sx={{
                                    position: 'sticky',
                                    top: 0,
                                    backgroundColor: '#f5f4f6',
                                    padding: 2,
                                    borderBottom: '1px solid #ddd',
                                    zIndex: 1
                                }}
                            >
                                <Typography sx={{
                                    textAlign: {
                                        xs: 'center',
                                        sm: 'left'
                                    }
                                }} variant="h6">Sipari Ayrntlar</Typography>
                            </Box>

                            {/* Kaydrlabilir erik */}
                            <Box
                                className="custom-scrollbar"
                                sx={{
                                    flex: 1, // eriin kalan tm alan kaplamasn salar
                                    overflowY: 'auto',
                                    padding: 2,
                                    marginBottom: '64px' // Sipari zeti blmnn ykseklii kadar boluk brakn
                                }}
                            >
                                <Box sx={{display: 'flex', flexDirection: 'column', gap: 2}}>
                                    {orders.map((order, index) => (
                                        <Card
                                            key={index}
                                            sx={{
                                                display: 'flex',
                                                marginBottom: 1,
                                                alignItems: 'center',
                                                justifyContent: 'space-between',
                                                padding: 2,
                                                borderRadius: 2,
                                                boxShadow: 2,
                                                backgroundColor: '#fff'
                                            }}
                                        >
                                            <CardContent sx={{flex: 1}}>
                                                <Box sx={{display: 'flex', alignItems: 'flex-start', gap: 2}}>
                                                    <Typography
                                                        variant="h6"
                                                        sx={{
                                                            fontWeight: 'bold',
                                                            transform: 'rotate(-10deg)',
                                                            color: '#333'
                                                        }}
                                                    >
                                                        {order.quantity}x
                                                    </Typography>
                                                    <Box>
                                                        <Typography variant="h6" sx={{fontWeight: 'bold'}}>
                                                            {order.product.productName}
                                                        </Typography>
                                                        <Typography variant="body2">
                                                            Boyut: {order.size}
                                                        </Typography>
                                                        {order.content && (
                                                            <Typography variant="body2">
                                                                erik: {order.content}
                                                                {(() => {
                                                                    const extraFee = order.product.contents.find(content => content.name === order.content).extraFee;
                                                                    if (extraFee !== 0)
                                                                        return ` (+${extraFee} )`;
                                                                })()}
                                                            </Typography>
                                                        )}
                                                        <Typography variant="body2" sx={{fontWeight: 'bold'}}>
                                                            Fiyat: {calculateOrderPrice(order)} 
                                                        </Typography>
                                                    </Box>
                                                </Box>
                                            </CardContent>
                                            <CardActions>
                                                <Box sx={{display: 'flex', flexDirection: 'column', gap: 2, width: 1}}>
                                                    <Button
                                                        size="small"
                                                        color="error"
                                                        variant="contained"
                                                        startIcon={<DeleteIcon/>}
                                                        sx={{textAlign: 'left'}}
                                                        onClick={() => handleDeleteOrder(index)}
                                                    >
                                                        Sil
                                                    </Button>
                                                    <Button
                                                        size="small"
                                                        color="primary"
                                                        variant="contained"
                                                        startIcon={<EditIcon/>}
                                                        sx={{textAlign: 'left'}}
                                                        onClick={() => handleEditOrder(index)}
                                                    >
                                                        Dzenle
                                                    </Button>
                                                </Box>
                                            </CardActions>
                                        </Card>
                                    ))}
                                </Box>
                            </Box>

                            {/* Sipari zeti Blm */}

                            <OrderSummary
                                orders={orders}
                                clearOrders={() => {
                                    setOrders([]);
                                    setShowCart(false);
                                }}
                                calculateOrderPrice={calculateOrderPrice}
                                calculateTotalPrice={calculateTotalPrice}/>


                        </Box>
                    </Grid>
                )}


            </Grid>


            {/* Sepet Dmesi */}
            {isMobile && orders.length > 0 && (
                <IconButton
                    sx={{
                        position: 'fixed',
                        bottom: 16,
                        right: 16,
                        color: 'white',
                        zIndex: 10,
                        backgroundColor: 'primary.dark',
                        boxShadow: 3,
                        '&:hover': {backgroundColor: 'primary.main'}
                    }}
                    onClick={handleCartToggle}
                    aria-label={showCart ? 'Kapat' : 'Sepet'}
                >
                    <Badge color="error" badgeContent={orders.length}>
                        {showCart ? <CloseIcon/> : <ShoppingCartIcon/>}
                    </Badge>
                </IconButton>
            )}

            {/* rn Seimi Dialog */}
            <Dialog open={open} onClose={handleClose} maxWidth="xs" fullWidth>
                <DialogContent dividers>
                    {selectedProduct ? (
                        <Box sx={{display: 'flex', flexDirection: 'column', gap: 2, alignItems: 'center'}}>
                            <Typography variant="h6" sx={{textAlign: 'center'}}>
                                {selectedProduct.productName}
                            </Typography>
                            <FormControl fullWidth error={sizeError}>
                                <InputLabel>Boyut</InputLabel>
                                <Select
                                    value={selectedSize}
                                    onChange={(e) => {
                                        setSelectedSize(e.target.value);
                                        setSizeError(false);
                                    }}
                                    label="Boyut"
                                    required
                                >
                                    {selectedProduct.sizes.map(size => (
                                        <MenuItem key={size.size} value={size.size}>
                                            <Box sx={{display: 'flex', justifyContent: 'space-around', width: '100%'}}>
                                                <Typography>{size.size}</Typography>
                                                {size.price > 0 && <Typography>+{size.price} </Typography>}
                                            </Box>
                                        </MenuItem>
                                    ))}
                                </Select>
                                {sizeError && <FormHelperText>Ltfen boyut seiniz</FormHelperText>}
                            </FormControl>
                            {selectedProduct.contents.length > 0 && (
                                <FormControl fullWidth>
                                    <InputLabel>erik</InputLabel>
                                    <Select
                                        value={selectedContent}
                                        onChange={(e) => setSelectedContent(e.target.value)}
                                        label="erik"
                                        required
                                    >
                                        {selectedProduct.contents.map(content => (
                                            <MenuItem key={content.name} value={content.name}>
                                                <Box sx={{
                                                    display: 'flex',
                                                    justifyContent: 'space-evenly',
                                                    width: '100%'
                                                }}>
                                                    <Typography>{content.name}</Typography>
                                                    {content.extraFee > 0 &&
                                                        <Typography>+{content.extraFee} </Typography>}
                                                </Box>
                                            </MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            )}
                            <TextField
                                sx={{
                                    '& input': {textAlign: 'center'}
                                }}
                                type="number"
                                label="Adet"
                                value={quantity}
                                onChange={(e) => setQuantity(parseInt(e.target.value, 10))}
                                fullWidth
                                InputProps={{inputProps: {min: 1}}}
                                required
                            />
                        </Box>
                    ) : (
                        <Typography variant="body1" sx={{textAlign: 'center'}}>
                            Ltfen bir rn sein.
                        </Typography>
                    )}
                </DialogContent>
                {selectedProduct && (
                    <DialogActions sx={{justifyContent: 'space-between'}}>
                        <Typography variant="subtitle1" sx={{fontStyle: 'italic', ml: 2}}>
                            {(() => {
                                if (selectedProduct && selectedProduct.sizes) {
                                    const size = selectedProduct.sizes.find(size => size.size === selectedSize);
                                    if (size) {
                                        const basePrice = size.price * quantity;
                                        const extraFee = selectedProduct.contents.find(content => content.name === selectedContent)?.extraFee || 0;
                                        return `Fiyat : ${basePrice + extraFee} `;
                                    }
                                }
                            })()}
                        </Typography>
                        <Stack direction="row" spacing={1}>
                            <Button onClick={handleClose}>ptal</Button>
                            <Button onClick={handleAddToOrder}
                                    variant="contained">{!!editOrderIndex ? 'Dzenle' : 'Ekle'}</Button>
                        </Stack>
                    </DialogActions>
                )}
            </Dialog>


        </>
    );
}
